.videos {
  display: grid;
  color: #555;
}
.videos .name {
  font-size: 23px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333;
}
@media (max-width: 1025px) {
  .videos .name {
    font-size: 50px;
  }
}
.videos .hr {
  height: 0.1px;
  background-color: #666;
  width: 1000px;
}
.videos .maneText {
  margin: 10px 0 0 0;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  font-size: 33px;
  color: #333;
}
@media (max-width: 1025px) {
  .videos .maneText {
    font-size: 55px;
  }
}
.videos .maneText .hr {
  height: 1px;
  background-color: #222;
  width: 96%;
}
.videos .videoComponent {
  margin: 16px 92.7px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.videos .videoComponent :nth-child(1) {
  margin: 20px 50px;
}
.videos .videoComponent .video {
  width: 1000px;
}
.videos .videoComponent .videosContent {
  width: 700px;
}
.videos .videoComponent .hr {
  background-color: #555;
  height: 0.4px;
  width: 900px;
}